.size-rating {
  display: inline-block;
  vertical-align: top;
  width: 24px;
  height: 24px;
  line-height: 24px;
  border-radius: 24px;
  background-color: #297BAE;
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  text-align: center;
}

.size-rating-small {
  width: 20px;
  height: 20px;
  line-height: 20px;
  margin-top: -1px;
  margin-bottom: -1px;
}

.size-rating-muted {
  background-color: #b4b4b4;
}
