.chart-zoom-tick {
  fill: #656565;
  font-size: 10px;
  text-anchor: middle;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.chart-zoom .zoom-overlay {
  fill: none;
  stroke: none;
  cursor: crosshair;
  pointer-events: all;
}

.chart-zoom .zoom-selection {
  fill: #656565;
  fill-opacity: 0.2;
  stroke: #656565;
  shape-rendering: crispEdges;
  cursor: move;
}

.chart-zoom .zoom-selection-handle {
  cursor: ew-resize;
  fill-opacity: 0;
  stroke: none;
}
